.imagen-logo {
  width: 4rem;
}

.hero {
  padding: 6rem 0 4rem 0;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.6) 100%), url(../img/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .hero {
    padding: 8rem 0 5rem 0;
  }
}

.bienvenida .informacion {
  width: 95%;
  max-width: 30rem;
}

.galeria .carousel {
  max-width: 70rem;
}
.galeria .imagen-carousel {
  object-fit: cover;
  object-position: center;
  aspect-ratio: 16/9;
}

.platillo .contendor-card {
  max-width: 35rem;
}

.newsletter .imagen {
  width: 75%;
}
.newsletter form {
  display: none;
}